import request from '../../utils/request.js'
export default {
	data() {
		return {
			categoryList: [],
			navIndex: 0, 
		};
	},
	mounted(){
		this.getCateGoryDatas();
	},
	methods: {
		async getCateGoryDatas(){
			this.categoryList = await request('/getCateGoryDatas')
		},
		changeNavIndex(navIndex){
			this.navIndex = navIndex
		}
	}
}
